
.container{
    display: flex;
    flex-direction:column;
    margin:auto;
    max-width: 90%;
    width:600px;
    margin-top: 60px;
    background: #fff;
    padding-bottom: 30px;
    margin-bottom: 10px;
}
.header{
    display:flex;
    flex-direction: column;
    align-items:center;
    gap:9px;
    width: 100%;
    margin-top: 30px;
}
.underline{
    width:61px;
    height:6px;
    background:#3c009d;
    border-radius: 9px;
}
.text{
    caret-color: transparent;
    color: #3c009d;
    font-size: 48px;
    font-weight: 700;
    font-family:Arial, Helvetica, sans-serif

}
.inputs{
    margin-top:55px;
    display:flex;
    flex-direction:column;
    gap:25px;
}
.input{
    display: flex;
    align-items: center;
    margin:auto;
    width:480px;
    height:80px;
    background:#eaeaea;
    border-radius: 6px;
}
.input img{
    margin:0px 30px;
} 
.input input{
    height:50px;
    width:400px;
    background:transparent;
    border:none;
    outline:none;
    color:#797979;
    font-size: 19px;
}
.forgot_password{
    caret-color: transparent;
    padding-left:62px;
    margin-top: 27px;
    color:#797979;
    font-size: 18px;
}
.forgot_password span{
    color:#4c00b4;
    cursor:pointer;
}
.submit-container{
    display: flex;
    gap:30px;
    margin:60px auto;
}
.submit{
    display: flex;
    justify-content: center;
    align-items: center;
    width:220px;
    height:59px;
    color:#fff;
    background: #4c00b4;
    border-radius: 50px;
    font-size: 19px;
    font-weight: 700;
    cursor:pointer;
    caret-color: transparent;
    text-decoration: none;
}
.gray{
    background-color:#797979;
    color:#eaeaea
}